section.about_ban {
  width: 100%;
  height: 960px;
  background-image: url("../../assets/images/About/Banner.png");
  background-size: contain;
  background-repeat: no-repeat;
}
.about_Con h2 {
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  color: #323232;
  margin-top: 198px;
}

.about_Con p {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  color: #4f4f4f;
}
section.ban_sec2 {
  margin-top: -130px;
}
.sec2_con {
  padding: 50px 10px;
}
section.ban_sec2 img {
  width: 100%;
  border: 10px solid #fff;
  box-shadow: 0px 0px 20px #ccc;
}
section.ban_sec2 p {
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #4f4f4f;
}
section.about_testim {
  margin-top: 150px;
  margin-bottom: 130px;
}
.our_his {
  background: linear-gradient(96.72deg, #fad0c4 6.25%, #e4a8e4 103.39%);
  border-radius: 10px;
  width: 100%;
  height: 453px;
  padding: 57px 70px;
  margin: 0 50px;
}
.our_his h2 {
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 60px;
  color: #323232;
}
@media only screen and (max-width: 1280px) {
  section.about_ban {
    background-size: contain;
  }
  section.ban_sec2 {
    margin-top: -290px;
  }
}


